import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
      token: "",
  },
  getters: {
      TOKEN: state => {
          return state.token;
      }
  },
  mutations: {
      SET_TOKEN: (state, payload) => {
          state.token = payload;
      }
  },
  actions: {
      setToken (context, token) {
          context.commit('SET_TOKEN', token)
      }
  }
});