<template>
  <div id="app">
    <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/register">Register</router-link> |
      <router-link to="/login">Login</router-link> |
      <router-link to="/chat">Chat</router-link>
    </div>
    <router-view @login="logged" @logout="logout" :isLogged="isLogged" />
  </div>
</template>

<script>
import { onLogin, onLogout } from "./vue-apollo.js";
export default {
  name: "App",
  data() {
    return {
      isLogged: false
    };
  },
  methods: {
    async logged() {
      await onLogin(this, localStorage.getItem("token"));
      this.isLogged = true;
      console.log("logged in");
    },
    async logout() {
      await onLogout(this);
      this.isLogged = false;
      console.log("logged out");
    }
  }
};
</script>

